
* {box-sizing: border-box;}

html, body {
  height: 100%;
}

html {
  font-family: 'Lucida Grande', Helvetica, Arial, sans-serif;
  background-color: #123;
}

body {
  margin: 0;
}

.flex-separator {
  flex: 1;
}

.start {
  display: flex;
  margin: 0px auto;
  // max-width: 920px;
  align-items: center;
  justify-content: center;
}

.title-header {
  display: block;
  text-decoration: none;
  text-align: center;
  background-color: #fff;
  position: relative;
  padding: 50px 0px 70px;
  flex: 1;

  .logo {
    padding: 30px 20px 0px;
    font-size: 0;

    img {width: 140px;}
  }

  .name {
    margin: 0px auto;
    padding: 20px 20px;
    max-width: 640px;

    h1, h2, p {
      margin: 0;
      padding: 0;
    }

    h1 {
      color: #234;
      font-size: 2.2em;

      a {
        text-decoration: none;
        color: inherit;
      }
    }

    h2 {
      font-size: 1em;
      font-weight: normal;
      color: #678;
      padding-bottom: 2em;
    }

    p {
      color: #456;
      font-size: 1.2em;
      padding-bottom: 1em;
    }
  }

  .links {
    a {
      display: inline-block;
      padding: 15px;
      text-align: center;
      text-decoration: none;
      flex: 1;
      color: #555;
      font-size: 1em;

      &:hover {
        color: #08f;
      }

      &:first-child {
        border-left: none;
      }

      &:last-child {
        border-right: none;
      }
    }
  }
}

section {
  padding: 20px;
}

.about-me {
  background-color: #ab0c43;
  margin: -20px -20px 40px;
  padding: 30px 20px;
  transform: rotate(-1deg);
  border-style: double;
  border-width: 4px;
  border-color: darken(#ab0c43, 10%);
  position: relative;
  overflow: hidden;

  .somehow-comet {
    z-index: -1;
    position: absolute;
    top: -20px;
    left: -50px;
    opacity: 0.1;
  }

  &-inner {
    transform: rotate(1deg);
    display: flex;
    padding: 20px;
    max-width: 960px;
    margin: 0px auto;
    justify-content: flex-end;

    h2 {
      font-size: 2em;
      margin: 0px;
      color: darken(#ab0c43, 30%);
      text-shadow: 0 0 2px rgba(0,0,0,0.1);
    }

    p {
      margin: 0;
      padding: 0 0 1em 0;
      color: darken(#ab0c43, 22%);
      text-shadow: 0 0 2px rgba(0,0,0,0.1);
      font-size: 1.2em;
    }

    .stuffs {
      max-width: 640px;
    }

    .buttons {
      a {
        display: inline-block;
        padding: 3px 0px;
        text-decoration: none;
        border-bottom: 2px solid darken(#ab0c43, 10%);
        color: darken(#ab0c43, 25%);
        transition: all 0.25s linear;

        &:hover {
          color: darken(#ab0c43, 30%);
          border-color: darken(#ab0c43, 25%);
        }
      }
    }
  }
}

.open-source {
  background: linear-gradient(45deg, #0044dd 0%,#2989d8 50%,#2989d8 50%,#0044ff 100%);
  box-shadow: 0 0 5px #123;
  padding: 40px 20px;
  margin: 20px 0px;
  border-radius: 100% 200px;

  h2 {
    color: #fff;
    font-size: 2em;
    text-align: center;
  }
}

.projects {
  display: flex;
  align-items: stretch;
  justify-content: space-around;
  flex-wrap: wrap;
  flex-grow: 3;
  text-align: center;
  border-radius: 2px;
}

.project {
  display: flex;
  position: relative;
  flex: 1;
  flex-direction: column;
  margin: 10px;
  background-color: #fff;
  border-radius: 2px;
  box-shadow: 0 0 5px rgba(0,0,0,0.2);
  min-width: 240px;
  transition: all 0.15s ease-out;
  top: 0px;

  &:hover {
    top: -4px;
    box-shadow: 0 2px 8px rgba(0,0,0,0.3);
  }

  .about {
    flex: 1;
    justify-content: center;
    align-items: center;
    padding: 20px;

    h3, p {
      margin: 0px;
    }
  }

  .links {
    display: flex;
    justify-content: center;
    align-items: stretch;
    border-top: 1px solid #eee;

    a {
      display: block;
      flex: 1;
      padding: 15px;
      text-decoration: none;
      color: inherit;

      border-right: 1px solid #eee;

      &:last-child {
        border-right: none;
      }
    }

    a:hover {
      color: #08f;
    }
  }
}

.document {
  background-color: #fff;
  padding: 0px 20px 40px;

  .content {
    max-width: 620px;
    margin: 0px auto;
    text-align: center;

    img {
      width: 100%;
      margin-bottom: 2em;
    }

    h1 {
      margin: 0px;
    }

    p {
      color: #345;
      font-size: 1.1em;
      margin: 0px;
      padding-bottom: 1.5em;
    }

    a {color: #08f;}
  }
}

.talks {
  background-color: #f4f4f4;
  box-shadow: 0 0 5px #123;
  border: 4px dashed rgba(0,0,0,0.2);
  transform: rotate(1deg);
  padding: 40px 40px 50px;
  margin: 40px -20px;
  position: relative;
  overflow: hidden;

  .slide-funk {
    z-index: -1;
    position: absolute;
    top: -40px;
    right: 0px;
    opacity: 0.1;
  }

  &-inner {
    transform: rotate(-1deg);

    & > p {
      display: flex;
      align-items: center;
      justify-content: center;

      color: #345;

      .offer {
        display: inline-block;
        background-color: #FFb700;
        color: #fff;
        padding: 3px 5px;
        margin: 0px 5px;
        font-size: 0.7em;
        // text-transform: uppercase;
        border-radius: 2px;
        vertical-align: middle;
        white-space: nowrap;
      }
    }
  }

  h2 {
    color: #111;
    font-size: 2em;
    text-align: center;
  }

  .talk-list {
    list-style-type: none;
    margin: 0px auto;
    max-width: 640px;
    padding: 20px 0px;
    text-align: center;

    .talk {
      padding: 20px 0px;

      h3 {
        transition: all 0.15s ease-in;
        min-height: 40px;
        font-size: 1.5em;
        margin: 0px;

        &:hover {
          font-size: 1.7em;
          color: #000;
        }

        a {
          text-decoration: none;
          color: #123;

          &:after {
            content: '↗';
            font-size: 0.8em;
            margin-left: 3px;
            opacity: 0.8;
          }
        }
      }

      p {
        margin: 0px;
        color: #444;
      }
    }
  }
}

.contact {
  position: relative;
  max-width: 640px;
  margin: 0px auto;
  padding: 25px 20px;

  .cavebob {
    position: absolute;
    z-index: -1;
    right: -20px;
    bottom: 0;
    width: 420px;
    max-width: 80%;
    opacity: 0.2;
  }

  @media(max-width: 480px) {
    font-size: 0.7em;
  }

  h2 {
    font-size: 2em;
    color: #fff;
    text-shadow: 0 0 5px #234;

    a {
      text-decoration: none;
      color: inherit;
    }
  }

  dt {
    display: flex;
    align-items: center;
    color: #bcd;
    font-size: 1.2em;

    .prefer {
      display: inline-block;
      background-color: #32ad64;
      color: #fff;
      padding: 3px 5px;
      margin: 0px 5px;
      font-size: 0.7em;
      // text-transform: uppercase;
      border-radius: 2px;
      vertical-align: middle;
    }
  }

  dd {
    margin: 0px;
    padding-bottom: 1em;
    font-size: 1.5em;

    a {
      display: inline-block;
      text-decoration: none;
      text-shadow: 0 0 5px #123;
      color: #08f;

      padding: 3px 0px;
      border-bottom: 2px solid #048;

      transition: all 0.25s linear;

      &:after {
        content: '↗';
        font-size: 0.8em;
        margin-left: 3px;
        opacity: 0.8;
      }

      &:hover {
        color: #0af;
        border-color: #0af;
      }
    }
  }
}

.quote {
  padding: 50px 20px;
  text-align: center;

  label {
    color: #cde;
    font-size: 1em;
  }

  h2 {
    color: #fff;
    text-decoration: 0 0 5px #234;
    margin: 0px;
    padding: 10px 0px;
    font-size: 1.6em;
  }
}

.copyright {
  border-top: 1px solid #012;
  box-shadow: inset 0 1px 0 #234;
  padding: 20px;
  margin-top: 20px;

  p {
    color: #abc;
    font-size: 0.9em;
    text-align: center;
  }
}
